.sidepanel-container {
  -ms-flex: 0 0 15.625em;
      flex: 0 0 15.625em;
  height: 100vh;
  
  display: -ms-flexbox;
  
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  border-right: 3px solid #f8f8f8;  
}

/* user profile */

.user-profile {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  border-bottom:1px solid #f2f2f2;
}

.user-profile > .title {
  font-family: 'muli-bold';
  font-size: 18px;
  letter-spacing: 0.3px;
  margin: 25px 0px 15px 0px;
  color: #24233A;
}

.user-profile > .photo {
  width: 75px;
  height: 75px;
  border-radius: 50%;
  -o-object-fit: cover;
     object-fit: cover;
  background-color: #E2E2E2;
}

.user-profile > .name {
  font-family: 'muli-regular';
  font-size: 14px;
  letter-spacing: 0.2px;
  margin: 5px 0px 15px 0px;
  color: #24233A;
}

/* categories */

.categories {
  width: 100%;
  display:-ms-flexbox;
  display:flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align:start;
      align-items:flex-start;
}

.categories > .title {
  font-family: 'muli-semi-bold';
  font-size: 13px;
  letter-spacing: 0.1px;
  color: #24233A; 
  margin: 20px 0px 15px 15px;  
}

.categories > .categories-list {
  width: 100%;
  -ms-flex: 0 0 100%;
      flex: 0 0 100%;
  display:-ms-flexbox;
  display:flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.categories-list > .category {
  width: 100%;
  -ms-flex: 0 0 25px;
      flex: 0 0 25px;
  padding: 9px 0px 0px 15px;
  font-family: 'muli-semi-bold';
  font-size: 14px;
  letter-spacing: 0.2px;
  color: #24233A; 
  cursor: pointer;
}

.sidepanel-container .category:hover {
  background-color: #F8F8F8;
}

.sidepanel-container .active {
  background-color: #E8E8E8 !important;
  color: #24233A;
}

.hide {
  display: none;
}

/* Hide side panel for mobile apps */

@media only screen and (max-width: 700px) {
  .sidepanel-container {
    display:none;
  }
}



