.display-container {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
  display:-ms-flexbox;
  display:flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  overflow:hidden;
}

/* search area */

.display-container > .search-area {
  width: 90%;
  -ms-flex: 0 0 45px;
      flex: 0 0 45px;
  margin: 15px 0px 10px 0px;
  border-bottom: 1px solid #d9d9d9;
  display:-ms-flexbox;
  display:flex;

}

/* search icon */

.search-area > .search-icon {
  width: 20px;
  height: 20px;
  margin: 15px 15px 0px 0px;
}

/* search input field */

.search-area > .search-input {
  width : 100%;
  font-family: muli-bold;
  font-size: 17px;
  color: #232423;
  border:none;
  outline:none;
}


.search-input::-webkit-input-placeholder {
  font-family: muli-bold;
  font-size: 17px;
  color: #91919C;
  opacity:1; 
  letter-spacing: 0.019em;
}


.search-input:-ms-input-placeholder {
  font-family: muli-bold;
  font-size: 17px;
  color: #91919C;
  opacity:1; 
  letter-spacing: 0.019em;
}


.search-input::-ms-input-placeholder {
  font-family: muli-bold;
  font-size: 17px;
  color: #91919C;
  opacity:1; 
  letter-spacing: 0.019em;
}


.search-input::placeholder {
  font-family: muli-bold;
  font-size: 17px;
  color: #91919C;
  opacity:1; 
  letter-spacing: 0.019em;
}

.search-input::-ms-input-placeholder { /* Internet Explorer 10-11 */
  font-family: muli-bold;
  font-size: 17px;
  color: #91919C;
  letter-spacing: 0.019em;    
}

.search-input::-ms-input-placeholder { /* Microsoft Edge */
  font-family: muli-bold;
  font-size: 17px;
  color: #91919C;
  letter-spacing: 0.019em;    
}

/* movies result */

.display-container > .movies-container {
  width: 100%;
  -ms-flex: 1 1 100%;
      flex: 1 1 100%;

  display: -ms-flexbox;

  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  overflow: scroll;
}

/* no movies */
.no-movies {
  width: 400px;
  height: 300px;
  text-align:center;
  font-family: muli-bold;
  font-size: 17px;
  color: #91919C;
}

/* spinner */
.spinner {
  width: 300px;
  height: 300px;  
  -o-object-fit: cover;  
     object-fit: cover;
}


/* for mobile view */
@media only screen and (max-width:700px) {

  .display-container > .search-area {
    width: 90%;
    -ms-flex: 0 0 45px;
        flex: 0 0 45px;
    margin: 10px 0px 10px 0px;
    border-bottom: 1px solid #d9d9d9;
    display:-ms-flexbox;
    display:flex;
  }

}


